import "bootstrap/dist/css/bootstrap.min.css";

import logo from "./bookkeeper-finder.png";
import "./App.css";
import { Form, Table, Row, Col } from "react-bootstrap";
import { useState } from "react";

function App() {
  const [country, setCountry] = useState("");

  return (
    <div>
      <Row>
        <Col md="1">
          <img style={{ maxWidth: "100%" }} src={logo} />
        </Col>
        <Col>
          <h1>Bookkeeper Finder</h1>
        </Col>
      </Row>
      <Form.Control
        as="select"
        custom
        onChange={(e) => setCountry(e.target.value)}
      >
        <option value="">Select</option>
        <option value="us">United States</option>
        <option value="gb">United Kingdom</option>
      </Form.Control>

      {country === "" && <div className="m-2">Please select a country</div>}
      {country !== "" && (
        <div className="m-2">
          <Table striped bordered hover>
            <thead>
              <tr>
                <th>#</th>
                <th>Name</th>
                <th>Website</th>
                <th>Email</th>
              </tr>
            </thead>
            <tbody>
              {country === "us" && (
                <tr>
                  <td>1</td>
                  <td>Advanced Bookeeping</td>
                  <td>https://www.advancedbookkeeping.com</td>
                  <td>info@advancedbookkeeping.com</td>
                </tr>
              )}
              {country === "gb" && (
                <tr>
                  <td>1</td>
                  <td>Bookeeping London</td>
                  <td>https://www.bookkeepinglondon.co.uk</td>
                  <td>info@bookkeepinglondon.co.uk</td>
                </tr>
              )}
            </tbody>
          </Table>
        </div>
      )}
    </div>
  );
}

export default App;
